@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro&display=swap);
*{margin:0;padding:0;box-sizing:border-box;--sidebar-background: rgba(90, 81, 81, 0.301)}.App{background-image:url(/static/media/background-main-small.0bb6502e.gif);background-size:cover;background-position:center center}body{margin:0;font-family:"Anonymous Pro", monospace;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}.slide-in{-webkit-animation-duration:1s;animation-duration:1s;-webkit-animation-name:slidein;animation-name:slidein}@-webkit-keyframes slidein{from{margin-left:100%;width:300%}to{margin-left:10%;width:100%}}@keyframes slidein{from{margin-left:100%;width:300%}to{margin-left:10%;width:100%}}.pulsate{-webkit-animation:pulsate 3s ease-out;animation:pulsate 3s ease-out;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@-webkit-keyframes pulsate{0%{color:teal;transform:scale(2)}50%{color:#855757;transform:scale(4)}100%{color:teal;transform:scale(2)}}@keyframes pulsate{0%{color:teal;transform:scale(2)}50%{color:#855757;transform:scale(4)}100%{color:teal;transform:scale(2)}}.page-container{position:relative;min-height:100vh}a{color:inherit;text-decoration:inherit}.selected{color:teal}.underline{text-decoration:underline}

#nav-container{display:flex;justify-content:center}#nav-bar{width:85%;display:flex;flex-direction:row;justify-content:space-between;align-items:center}#nav-right{width:45%;display:flex;flex-direction:row;justify-content:space-between;align-items:center}#nav-right #contact-link{font-size:1.3rem;text-decoration:underline}#nav-right .active{color:teal}#logo-container{z-index:1}#logo{width:120px;z-index:1}#logo:hover{cursor:pointer}#social-container img{height:70px}@media all and (max-width: 1023px){#nav-right{width:60%}}@media all and (max-width: 639px){#nav-bar{width:70%}#nav-right{display:flex;flex-direction:column;justify-content:center;align-items:center;padding-top:20px}#social-container{padding-top:20px}#social-container img{height:50px}}@media all and (max-width: 414px){#nav-bar{width:80%;display:flex;flex-direction:column;justify-content:center;align-items:center}#nav-right{display:flex;flex-direction:column;justify-content:center;align-items:center;padding-top:20px}#social-container{padding-top:20px}#social-container img{height:50px}}

#intro-container{display:flex;flex-direction:column;justify-content:space-evenly;align-items:flex-start;position:absolute;max-width:100%;height:15rem;margin-top:10%;margin-left:8rem;justify-content:space-evenly;font-size:3rem}#pointer-container{position:relative;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;width:60%;padding:2rem;top:calc(100vh / 2 + 12rem);margin-left:30%;height:15rem;border:10px solid #000;border-radius:20px;padding:2rem;font-size:1.1rem;background:rgba(180,164,182,0.514)}#pointer-container h4{font-size:1.3rem}#pointer-container a:hover{color:teal}#tagline{font-size:1.2rem}@media all and (max-width: 1023px){#intro-container{margin-top:7%;margin-left:6rem;padding-right:2rem;font-size:2rem}#intro-container p{font-size:1.2rem}#pointer-container{margin-left:20%}}@media all and (max-width: 639px){#intro-container{margin-top:20%;margin-left:5rem;padding-right:3rem}#pointer-container{top:calc(100vh / 2 + 8rem);width:70%;margin-left:15%;padding:1rem;height:20rem;text-align:center}#pointer-container h4{font-size:1.2rem}}

.sidebar{position:fixed;width:100vh;height:4rem;padding-top:1rem;background:var(--sidebar-background);text-align:center}.sidebar:hover{cursor:pointer;background-color:teal}.left-sidebar{transform-origin:left top;transform:rotate(-90deg);left:0;top:100%}.right-sidebar{transform-origin:right top;transform:rotate(90deg);right:0;top:100%}@media all and (max-width: 639px){.sidebar{height:2rem;padding-top:0}.sidebar h2{font-size:1.3rem}}

#about-page-container{width:90%;padding-top:120px;line-height:1.3;margin-left:10%;display:flex;flex-direction:row;justify-content:space-between;align-items:flex-start}#bio-container{position:relative;width:45%}#bio-container p{font-size:1.1rem}#image-section{margin-top:5rem;text-align:center;width:75%}#image-section img{margin-top:2rem;height:300px;border-radius:10px}#what-section{border:10px solid #dcd3df;border-radius:20px;padding:2rem;font-size:1.1rem;background:rgba(180,164,182,0.514);width:40%;margin-bottom:50px;background:teal;color:#e2d9d9}#what-section ul{line-height:1.6}#what-section li{padding-bottom:0.8rem}#what-section:hover{transition:ease-in 1s;transform:scale(1.05);background:#086868}#qualifications-section{padding-bottom:1.5rem}#knowledge-inner-list{padding-left:1.5rem}#learning-container,#relearning-container{padding-top:3rem}#disclaimer-section{margin-top:4rem;padding:1rem 0;border-top:1px dotted white;border-bottom:1px dotted white}.explanation{font-size:0.9rem}@media all and (max-width: 1023px){#about-page-container{display:flex;flex-direction:column;justify-content:center;align-items:center;padding-top:60px;margin-left:5%}#bio-container{width:75%}#image-section{align-content:center;width:100%}#what-section{margin-left:0;width:80%;margin:100px 0}}@media all and (max-width: 639px){#about-page-container li{padding-top:2rem}#bio-container{width:70%}#bio-container p{font-size:1rem}#what-section{margin-left:0;width:60%;margin:100px 0;padding:10px}}@media all and (max-width: 414px){#about-page-container li{padding-top:2rem}#bio-container{width:70%}#bio-container h1{font-size:1.1rem;text-align:center}#bio-container p{font-size:0.9rem;text-align:center}#what-section{margin-left:0;width:60%;margin:100px 0;padding:10px}#what-section h1{font-size:0.9rem;text-align:center}#what-section ul{font-size:0.8rem;text-align:center}#image-section{width:100%}#image-section img{height:200px;border-radius:10px}}

#portfolio-page-container{min-height:100vh;width:100%;display:flex;flex-direction:column;justify-content:flex-start;align-items:center;padding-top:40px;line-height:1.3}#portfolio-section{width:90%;padding:0 2rem;margin:2rem 0;display:grid;grid-template-columns:repeat(2, 1fr);justify-items:center}.portfolio-card{margin-top:2rem;height:calc((100vh / 3) + 100px);width:calc(100vw / 3);border:10px solid #dcd3df;border-radius:20px;padding:2rem;font-size:1.1rem;background:rgba(180,164,182,0.514);perspective:1000px;box-shadow:0px 0px 2px 1px black}.card-content{position:relative;width:100%;height:100%;transition:transform 1s;transform-style:preserve-3d}.portfolio-card:hover .card-content{transform:rotateY(180deg)}.card-front,.card-back{position:absolute;width:100%;height:100%;-webkit-backface-visibility:hidden;backface-visibility:hidden}.card-front{display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}.card-title{font-size:larger;text-decoration:underline;text-align:center}.card-subtitle{font-size:medium;text-align:center}.card-back{display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;transform:rotateY(180deg)}.card-description{margin-top:1.5rem}.card-links{margin-top:1.5rem;line-height:1.5;text-align:center;text-decoration:underline;color:white}.card-links a:hover{color:pink;cursor:pointer}.card-links .card-image{max-height:200px}#gchss-card{background:linear-gradient(#ccc6c6, rgba(156,180,180,0.842)),url(/static/media/me.6a7d7103.jpg);background-size:cover;background-position:center center}#count-of-monte-crypto-card{background:linear-gradient(#ccc6c6, rgba(156,180,180,0.842)),url(/static/media/count-of-monte-crypto.de079e6a.png);background-size:cover;background-position:center center}#portfolio-website-card{background:linear-gradient(#ccc6c6, rgba(156,180,180,0.842)),url(/static/media/portfolio-website.26670fb0.png);background-size:cover;background-position:center center}@media all and (max-width: 1023px){h1{font-size:1.4rem}.portfolio-card{width:75%}#portfolio-section{grid-template-columns:repeat(1, 1fr)}}@media all and (max-width: 639px){#portfolio-section{grid-template-columns:repeat(1, 1fr)}}

#contact-page-container{min-height:100vh;display:flex;flex-direction:row;justify-content:center;align-items:center}#contact-form-section{width:100%;height:500px;flex-shrink:0}#contact-form{display:flex;flex-direction:column;justify-content:center;align-items:center}#contact-form input{width:50%;height:30px;margin-bottom:20px;border-radius:10px;padding:10px}#contact-form #textarea{height:50px}#contact-btn{font-size:1.2rem;padding:10px;border-radius:10px;background:teal;color:white}

